.Container {
  position: absolute;

  width: 100%;
  height: 100%;
  background: grey;
  border-radius: 10px;
}

.Link {
  padding: 10px;
  display: block;
  line-height: 30px;
  text-decoration: none;
  color: black;
  font-weight: 400;
}

.Link:hover {
  transition-duration: 3s;
  background: lightgrey;
}

.Selection {
  font-weight: 900;
  padding: 20px;
  text-align: center;
}
